#comments .email-input {
  font-size: 1em;
  font-weight: 400;
  width: 650px;
  width: 100%;
  height: 35px;
  border-radius: 5px;
  margin-top: 10px; }

#comments .message {
  font-size: 1em;
  font-weight: 400;
  width: 500px; }

#comments .emailSubmitButton {
  font-size: 18px !important;
  width: 100% !important;
  height: 35px;
  color: white;
  border: solid;
  border-color: black;
  border-width: 1px !important;
  border-radius: 5px !important;
  margin-top: 15px;
  text-align: center;
  vertical-align: middle;
  padding-top: 4px;
  cursor: pointer; }

#comments .emailCancelButton {
  font-size: 18px !important;
  width: 100% !important;
  height: 35px;
  color: black;
  background-color: white;
  border: solid;
  border-color: darkgrey;
  border-width: 1px !important;
  border-radius: 5px !important;
  margin-top: 5px;
  text-align: center;
  vertical-align: middle;
  padding-top: 4px;
  cursor: pointer; }



