#comments .linkImagesml {
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-height: 140px;
  max-width: 190px;
  border-radius: 5px !important; }

#comments .linkTitlesml {
  font-weight: bold;
  margin-top: 10px; }

#comments .linkDescriptionsml {
  margin-top: 7px; }

#comments .linkComponentsml {
  word-wrap: break-word;
  border-radius: 3px;
  width: 190px; }

@media only screen and (max-width: 475px) {
  #comments .linkImagesml {
    max-width: 100%;
    margin-top: 20px; }
  #comments .linkRow {
    height: 100%; }
  #comments .linkImageSubmitButton {
    width: 80px !important;
    font-size: 14px !important; } }

@media only screen and (max-width: 350px) {
  #comments .linkImageSubmitButton {
    width: 60px !important;
    font-size: 10px !important;
    padding-top: 8px; }
  #comments .linkImageCharacterCount {
    font-size: 10px !important; } }



