body.modal-open {
  overflow: visible; }

html {
  height: auto !important;
  min-height: auto !important; }

#comments .modal {
  overflow-y: auto !important;
  background-color: rgba(0, 0, 0, 0.4); }

#comments .email-input {
  font-size: 1em;
  font-weight: 400;
  width: 650px;
  width: 100%;
  height: 35px;
  border-radius: 5px;
  margin-top: 15px; }

#comments .message {
  font-size: 1em;
  font-weight: 400;
  width: 500px; }

#comments .emailSubmitButton {
  font-size: 18px !important;
  width: 100% !important;
  height: 35px;
  color: white;
  border: solid;
  border-color: black;
  border-width: 1px !important;
  border-radius: 5px !important;
  margin-top: 15px;
  text-align: center;
  vertical-align: middle;
  padding-top: 4px; }

#comments .conversationCloseButton {
  font-size: 18px !important;
  width: 80px !important;
  height: 35px;
  color: black;
  background-color: white;
  border: solid;
  border-color: darkgrey;
  border-width: 1px !important;
  border-radius: 5px !important;
  margin-top: 5px;
  text-align: center;
  vertical-align: middle;
  padding-top: 4px;
  margin-left: calc(100% - 80px);
  cursor: pointer; }

#comments .conversationCloseButton:hover {
  background-color: #f2f2f2 !important; }

#comments .modal {
  overflow: auto !important;
  background-color: rgba(0, 0, 0, 0.4); }

#comments .dialogBody {
  border-radius: 6px; }



