#comments .linkImagesmlart {
  max-height: 140px !important;
  max-width: 190px !important;
  border-radius: 3px !important;
  margin-left: auto;
  margin-right: auto;
  word-wrap: break-word;
  overflow-wrap: break-word; }

#comments .linkTitlesmlart {
  font-weight: bold;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto; }

#comments .linkDescriptionsmlart {
  margin-top: 7px;
  margin-left: auto;
  margin-right: auto; }

#comments .linkComponentsmlart {
  border-radius: 3px !important;
  max-width: 190px;
  margin-bottom: 10px;
  word-wrap: break-word; }

#comments .linkRow {
  height: 100%; }

